import React, { useEffect, useState } from "react";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogTitle from "@mui/material/DialogTitle";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import "../QuickScanPopup/QuickScanPopup.css";
import UploadFileImage from "../../images/uploadimage.png";
import { useNavigate } from "react-router-dom";



/////////////SET REDUX//////////////
import { useDispatch } from 'react-redux';
import * as actionCreator from "../../state/Action/action"
import { bindActionCreators } from 'redux';
///////////////SET REDUX//////////////

//////////////GET REDUX//////////////
import { useSelector } from 'react-redux';
///////////////GET REDUX//////////////
import axios from "../../config/axios";

const QuickScanPopup = ({ showPopup, hidePopup }) => {

  
/////////////SET REDUX//////////////
const dispatch = useDispatch()
const action = bindActionCreators(actionCreator, dispatch)
/////////////SET REDUX//////////////

/////////////GET REDUX//////////////
const state = useSelector((state) => state.LogIn)
/////////////GET REDUX//////////////


useEffect(()=>{
  if(!state){
    navigate('/login')
  }
},[state])

  const [front ,setFront]=useState(null)
  const [back ,setBack]=useState(null)

  const [img1,setImg1] = useState(UploadFileImage)
  const [img2,setImg2] = useState(UploadFileImage)


  const scanData = async()=>{
    const newForm = new FormData()
    newForm.append('front',front)
    newForm.append('back',back)
    try{
      hidePopup()
      action.SearchStart()
      const res = await axios({
        method:'post',
        url:'company/scan',
        data:newForm,
        headers: {
          'Content-Type': 'multipart/form-data',
          'Authorization': `Bearer ${state.token}`
      }
      })
      action.SuccessMessage({
        title:'Success',
        txt:res.data.message
      })
      console.log(res.data);
    }catch(err){
      console.log(err);
      action.ErrorMessage({
        title:'Error',
        txt: err.response.data.message 
      })
    }finally{
      action.SearchEnd()
    }
  }


  const [open, setOpen] = useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("md"));

  const handleClose = () => {
    setOpen(false);
  };

  const navigate = useNavigate()

  return (
   <div>
      <Dialog
        fullScreen={fullScreen}
        open={showPopup}
        onBackdropClick={() => hidePopup()}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">
          <div style={{
            display:'flex',
            justifyContent:'space-between'
          }}>
            <span>{"Quick Document Scan"}</span>
            <span style={{color:'red',fontSize:'24px',cursor:'pointer'}} onClick={()=>hidePopup()}>x</span>
          </div>
        </DialogTitle>
        <DialogActions style={{ paddingLeft: '2rem', paddingRight: '2rem'}}>
        <label htmlFor="file1" style={{margin:'2px'}} className="InputField_main">
          <div className="InputField_main">
            <input
              type="file"
              id="file1"
              className="input_css"
              accept="png, jpg,jpeg"
              onChange={(e)=>{
                setFront(e.target.files[0])
                setImg1(URL.createObjectURL(e.target.files[0]))
              }}
            />
            <img
              src={img1}
              for="file1"
              alt="image"
              className="uploadImage_css"
            />
            <label htmlFor="file1" className="text_css_input">Upload Front Side</label>
          </div>
          </label>
          <label htmlFor="file2" style={{margin:'2px'}} className="InputField_main">
          <div  className="InputField_main">
            <input
              type="file"
              id="file2"
              className="input_css"
              accept="png, jpg,jpeg"
              onChange={(e)=>{
                setBack(e.target.files[0])
                setImg2(URL.createObjectURL(e.target.files[0]))
              }}
            />
            {/* <label htmlFor="file2" > */}
            <img
              src={img2}
              alt="image"
              className="uploadImage_css"
              for="file2"
            />
            <label htmlFor="file2" className="text_css_input">Upload Back Side</label>
            {/* </label> */}
          </div>
          </label>
          
        </DialogActions>
        <DialogActions style={{ display: 'flex', justifyContent: 'center' }}>
          <div className="scanBotn" onClick={scanData}>
            Scan
          </div>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default QuickScanPopup;
