import React, { useCallback, useRef, useState } from "react";
import Webcam from "react-webcam";
const WebcamStreamCapture = (p) => { 
    const webcamRef = useRef(null);
    const mediaRecorderRef = useRef(null);
    const [capturing, setCapturing] = useState(false);
    const [recordedChunks, setRecordedChunks] = useState([]);
  
    const handleStartCaptureClick = useCallback(() => {
      setCapturing(true);
      mediaRecorderRef.current = new MediaRecorder(webcamRef.current.stream, {
        mimeType: "video/webm"
      });
      mediaRecorderRef.current.addEventListener(
        "dataavailable",
        handleDataAvailable
      );
      mediaRecorderRef.current.start();
    }, [webcamRef, setCapturing, mediaRecorderRef]);
  
    const handleDataAvailable = React.useCallback(
      ({ data }) => {
        if (data.size > 0) {
          setRecordedChunks((prev) => prev.concat(data));
        }
      },
      [setRecordedChunks]
    );
  
    const handleStopCaptureClick = useCallback(() => {
      mediaRecorderRef.current.stop();
      setCapturing(false);
      
    }, [mediaRecorderRef, webcamRef, setCapturing]);
  
    const handleDownload = useCallback(() => {
      if (recordedChunks.length>0) {
        const blob = new Blob(recordedChunks, {
          type: "video/webm"
        });
        const files = new File([blob],'video.webm',{lastModified:new Date(),type: "video/webm"})
        console.log(files);
        p.setVideo(files)
        p.setData(URL.createObjectURL(blob))
        p.offf(false)
        setRecordedChunks([]);
      }
    }, [recordedChunks]);
  
    return (
      <>
        <div style={{position:'relative',justifyContent:'center',display:'flex',alignItems:'center'}}>
        <Webcam
                  ref={webcamRef}
                  audio={false}
                  height='50%'
                  width='60%'
                  forceScreenshotSourceSize
                  videoConstraints={{
                    height: 720,
                    width: 1280,
                    facingMode: "user",
                  }}
                  screenshotQuality={1}

                />
                {!capturing ? (
                  <>
                  <div className="photo_capture_button"
                        onClick={handleStartCaptureClick}
                      >
                        <i style={{padding:'20px',backgroundColor:'gray',borderRadius:'50%'}}>
                          start
                        </i>
                      </div>
                  
              {recordedChunks.length > 0 && (
                <button className="btn btn-success b" onClick={handleDownload}>preview</button>          
              )}
                  </>
                        
                        ) : (
                            <div className="photo_capture_button"
                                onClick={()=>{
                                    handleStopCaptureClick();
                                    handleDownload();
                                }}
                            >
                  <i style={{backgroundColor:'red',padding:'20px',fontWeight:'bolder',borderRadius:'100%'}}>
                    stop
                  </i>
                </div>
                      
            )}

                
        </div>
        
        
      </>
    );
  };
  
export default WebcamStreamCapture
  

  